// 导入 axios 实例
import service from "..";

// 获取首页各个模块信息
export function getHomePage() {
  return service({
    method: 'GET',
    url: '/homepage/block/page'
  })
}

// 获取歌单分类
export function getSongListClassification() {
  return service({
    method: 'GET',
    url: '/playlist/catlist'
  })
}

// 获取热门的歌单分类
export function getHotSongListClassification() {
  return service({
    method: 'GET',
    url: '/playlist/hot'
  })
}

// 获取歌单 (网友精选碟) 默认每次请求一页 100个歌单
export function getSongList(cat, limit, offset) {
  return service({
    method: 'GET',
    url: `/top/playlist?cat=${cat}&limit=${limit}&offset=${offset}`
  })
}

// 获取精品歌单标签列表
export function getHighQualityTags() {
  return service({
    method: 'GET',
    url: '/playlist/highquality/tags'
  })
}

// 获取精品歌单
export function getHighQualitySongList(cat) {
  return service({
    method: 'GET',
    url: `/top/playlist/highquality?cat=${cat}`
  })
}

// 获取所有榜单
export function getAllLists() {
  return service({
    method: 'GET',
    url: '/toplist'
  })
}

// 获取新歌速递歌曲
export function getNewSong(data) {
  return service({
    method: 'GET',
    url: `/top/song?type=${data}`
  })
}

// 获取新碟上架专辑，分为 weekData 和 monthData
export function getNewDisc(area, type) {
  return service({
    method: 'GET',
    url: `/top/album?offset=0&limit=30&area=${area}&type=${type}`
  })
}

// 获取歌手分类列表 最多返回100条数据，且页面渲染只能渲染到第 96 (16行)
export function getSingersClassification(type, area, initial) {
  return service({
    method: 'GET',
    url: `/artist/list?type=${type}&area=${area}&initial=${initial}&offset=0&limit=100`
  })
}

// 获取歌手详情 
export function getArtistDetail(id) {
  return service({
    method: 'GET',
    url: `/artist/detail?id=${id}`
  })
}

// 获取每日推荐歌单(需要登录，携带 cookie)
export function getDailyRecommendedSongList(cookie) {
  return service({
    method: 'GET',
    url: `/recommend/resource?cookie=${cookie}`
  })
}

// 获取每日推荐歌曲(需要登录，携带 cookie)
export function getDailyRecommendedSongs(cookie) {
  return service({
    method: 'GET',
    url: `/recommend/songs?cookie=${cookie}`
  })
}

// 获取推荐歌单
export function getRecommendedSongList(limit) {
  return service({
    method: 'GET',
    url: `/personalized?limit=${limit}`
  })
}

// 获取热门电台
export function getHotPodcasts(limit) {
  return service({
    method: 'GET',
    url: `/dj/hot?limit=${limit}`
  })
}

// 独家放送(入口列表)
export function getExclusiveRelease() {
  return service({
    method: 'GET',
    url: '/personalized/privatecontent'
  })
}

// 获取独家放送列表
export function getPrivatecontentList(limit, offset) {
  return service({
    method: 'GET',
    url: `/personalized/privatecontent/list?limit=${limit}&offset=${offset}`
  })
}

// 获取推荐 mv 
export function getPersonalizedMv() {
  return service({
    method: 'GET',
    url: '/personalized/mv'
  })
}

// 获取历史日推可用日期列表(需要登录，携带 cookie)
export function getHistoryRecommend(cookie) {
  return service({
    method: 'GET',
    url: `/history/recommend/songs?cookie=${cookie}`
  })
}

// 获取历史日推详情数据(需要登录，携带 cookie)
export function getHistoryRecommendDetail(cookie, date) {
  return service({
    method: 'GET',
    url: `/history/recommend/songs/detail?date=${date}&cookie=${cookie}`
  })
}