// 导入 eventBus 模块
import bus from './eventBus.js';
import { getSingersClassification, getArtistDetail } from '@/request/api/discoverMusic.js';
export default {
  data() {
    return {
      // 语种
      area: -1,
      // 分类
      type: -1,
      // 筛选
      initial: -1,
      // 要渲染的歌手数据
      artists: [],
      // 请求回来的原数据
      resData: [],
      // 渲染总数
      total: 0,
      // 每次插入渲染的 DOM 数量
      once: 6,
      // 当前已经渲染 DOM 的总数，从 24 开始，因为一开始已经渲染了 24 位歌手
      countOfRender: 24,
      // 是否显示加载中
      isLoading: 1
    };
  },

  // 接收父组件传过来的值
  props: ['scrollTop'],

  mounted() {
    // 请求歌手数据
    this.getSingers();
  },

  created() {
    bus.on('area', val => {
      this.area = val;
    }), bus.on('type', val => {
      this.type = val;
    }), bus.on('initial', val => {
      this.initial = val;
    });
  },

  methods: {
    // 请求歌手数据
    async getSingers() {
      let res = await getSingersClassification(this.type, this.area, this.initial);
      console.log(res); // 把请求回来的原数据放到存放原数据的暂存数组中(该数组不是渲染数据的数组)

      this.resData = res.data.artists; // console.log(this.resData);
      // 获取歌手详情来判断歌手是否有身份图标(请求太频繁！)
      // for (var i = 0; i < 24; i++) {
      //   let res1 = await getArtistDetail(this.resData[i].id)
      //   // console.log(res1);
      //   if (res1.data.data.identify && res1.data.data.identify.imageUrl !== null) {
      //     console.log('下标', i, '有 identify 图标！');
      //     // 标志该歌手身份
      //     this.resData[i].isIdentify = 1
      //   }
      // }
      // 关闭加载组件

      this.isLoading = 0; // 渲染总数

      this.total = this.resData.length; // 刚开始只渲染 24 条数据

      this.artists = this.resData.slice(0, 24); // 重置当前已经渲染 DOM 的总数

      this.countOfRender = 24;
    },

    // 每次只渲染指定条数据(每次只加指定条数据到渲染数组中)
    async scrollLoad() {
      // console.log(this.countOfRender, this.total);
      const temp = [];

      for (let i = 0; i < this.once; i++) {
        // 判断歌手是否有身份图标，从下标 countOfRender 的开始(请求太频繁！)
        // let res = await getArtistDetail(this.resData[this.countOfRender + i].id)
        // // console.log(res);
        // if (res.data.data.identify && res.data.data.identify.imageUrl !== null) {
        //   console.log('下标', (this.countOfRender + i), '有 identify 图标！');
        //   // 标志该歌手身份
        //   this.resData[this.countOfRender + i].isIdentify = 1
        // }
        // 当 DOM 渲染完就退出
        if (this.countOfRender >= this.total) return; // 还没渲染完继续添加
        else {
          // 把请求回来的原数据先加到暂存数组中
          temp.push(this.resData[this.countOfRender]); // 当前已经渲染 DOM 的总数

          this.countOfRender += 1;
        }
      } // 然后把暂存数组的数据加到需要渲染的数组中，concat() 将一个数组合并到另一个数组


      this.artists = this.artists.concat(temp); // console.log('temp', temp);
    },

    // 去往歌手页
    goSingerPage(id) {
      this.$router.push({
        path: '/SingerPage',
        query: {
          id: id
        }
      });
    }

  },
  watch: {
    area() {
      // 显示加载
      this.isLoading = 1; // 请求歌手数据

      this.getSingers();
    },

    type() {
      this.isLoading = 1;
      this.getSingers();
    },

    initial() {
      this.isLoading = 1;
      this.getSingers();
    },

    // 监听从父组件 DiscoverSinger.vue 接收回来的 scrollTop 
    scrollTop() {
      // console.log('子：' + this.scrollTop);
      this.scrollLoad();
    }

  }
};