// 导入 eventBus 模块
import bus from './eventBus.js';
import { getSingersClassification } from '@/request/api/discoverMusic.js';
export default {
  data() {
    return {
      // 语种
      area: -1,
      // 分类
      type: -1,
      // 筛选
      initial: -1,
      // 接收的歌手数据
      artists: [],
      // 可选语种
      areas: [{
        id: -1,
        name: '全部'
      }, {
        id: 7,
        name: '华语'
      }, {
        id: 96,
        name: '欧美'
      }, {
        id: 8,
        name: '日本'
      }, {
        id: 16,
        name: '韩国'
      }, {
        id: 0,
        name: '其他'
      }],
      // 可选分类
      types: [{
        id: -1,
        name: '全部'
      }, {
        id: 1,
        name: '男歌手'
      }, {
        id: 2,
        name: '女歌手'
      }, {
        id: 3,
        name: '乐队组合'
      }],
      // 按字母筛选
      initialItems: ['a', 'b', 'c', 'd', 'e', 'f', 'g', 'h', 'i', 'j', 'k', 'l', 'm', 'n', 'o', 'p', 'q', 'r', 's', 't', 'u', 'v', 'w', 'x', 'y', 'z'],
      //当前语种
      activeArea: -1,
      //当前分类
      activeType: -1,
      //当前筛选
      activeInitial: -1
    };
  },

  mounted() {
    // 请求歌手数据
    this.getSingers();
  },

  methods: {
    // 请求歌手数据
    async getSingers() {
      let res = await getSingersClassification(this.type, this.area, this.initial); // console.log(res);

      this.artists = res.data.artists; // console.log(this.artists);
    },

    // 换语种
    changeArea(area) {
      this.area = area; // 请求歌手数据

      this.getSingers();
      bus.emit('area', this.area); //选项添加 active 类

      this.activeArea = area;
    },

    // 换分类
    changeType(type) {
      this.type = type;
      this.getSingers();
      bus.emit('type', this.type);
      this.activeType = type;
    },

    // 换字母
    changeInitial(initial) {
      this.initial = initial;
      this.getSingers();
      bus.emit('initial', this.initial);
      this.activeInitial = initial;
    }

  }
};