// 导入子组件
import SingersClassification from '@/components/menu/DiscoverMusic/DiscoverSinger/SingersClassification.vue';
import SingersList from '@/components/menu/DiscoverMusic/DiscoverSinger/SingersList.vue';
export default {
  data() {
    return {
      // 传给子组件的
      scrollTop: 0
    };
  },

  components: {
    SingersClassification,
    SingersList
  },

  created() {
    this.listenerFunction();
  },

  beforeDestroy() {
    // 去除监听器
    document.removeEventListener("scroll", this.listenerFunction);
  },

  methods: {
    // 给监听器绑定滚动方法 handleScroll()
    listenerFunction(e) {
      // 因为属于在主页面使用滚动事件，故可对 document 添加监听器
      document.addEventListener('scroll', this.handleScroll, true);
    },

    // 滚动方法
    handleScroll() {
      let a = this.$refs.big_container.scrollTop; // 每滚动 200 输出一次，SingersList 大约为 1 行歌手的距离

      if (a % 200 == 0) {
        this.scrollTop = a; // console.log('父：' + this.scrollTop);
      }
    }

  }
};